.footer {
    background: linear-gradient(to right, #101B2D, #233D5E);
    padding: 20px;
    text-align: center;
    z-index: 2;
    align-content: flex-end;
    margin-top: auto;
  }
  
  .footer-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
  }
  
  .footer-link {
    color: #fff;
    margin: 0 10px;
    text-decoration: none;
    font-size: 14px;
  }
  
  .footer-link:hover {
    text-decoration: underline;
  }
  
  .footer-link:active {
    color: #ccc;
  }